import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import axios from 'axios'

/*
	,statemutation
	,action
*/
export default new Vuex.Store({
  // 
  state: {
    token: '',
    userInfo: null // 
  },
  // 
  mutations: {
    token(state, data) {
      state.token = data
    },
    userInfo(state, data) {
      state.userInfo = data
    }
  },

  // ,store,statecommit
  actions: {
    token({
      commit
    }, data) {
      // actionmutation
      commit('token', data) //this.$route.commit('token',token)
    },
    myAction({
      state,
      commit
    }, data) {
      // tokenid
      return axios.post('/user2/car/add', {
        token: state.token,
        goodsId: data.goodsId,
        goodsNum: data.goodsNum,
        goodsName: data.goodsName,
        goodsPrice: data.goodsPrice,
        goodsImg:data.goodsImg
      })
    }
  }
})


