<template>
  <div class="list-container">
    <h2>
      <i class="iconfont ic" @click="toSort">&#xe637;</i>
      
    </h2>
    <div class="result">
      <span>970</span>
      <span @click="show=true">
        <i class="iconfont ic-two">&#xe634;</i>
      </span>
      <van-action-sheet v-model="show">
        <div class="select-B">
          <p></p>
          <p>
            <span @click="show=false"></span>
            <span>
              
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span></span>
            <span>
              
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span></span>
            <span>
              
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span></span>
            <span>
              
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p @click="show=false">
            <span></span>
            <span>
              
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <p class="lastOne" @click="show=false">
            <span></span>
            <span>
              
              <i class="iconfont">&#xe64c;</i>
            </span>
          </p>
          <div
            class="btn"
            @click="show=false"
            @touchstart="touchstart()"
            @touchend="touchend()"
            :class="{touchColor:whether}"
          ></div>
        </div>
      </van-action-sheet>
    </div>

    <!-- <van-action-sheet v-model="show" :actions="actions" @select="onSelect" /> -->
    <!-- 
            1.mt-loadmore
            2.loadToploadBottomallLoadedref
            3.auto-fillfalse
    -->
    <mt-loadmore
      :top-method="loadTop"
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      ref="loadmore"
      :auto-fill="false"
    >
      <!-- v-for -->
      <ul class="list">
        <!-- mongodbid_  _id -->
        <li
          v-for="(item,index) in list"
          :key="index"
          class="list-content"
          @click="toDetail(item._id)"
        >
          <div class="pic">
            <img :src="$commonUrl+'/static'+item.url" />
          </div>
          <div class="pic-introduction">
            <p class="frist">{{item.name}}</p>
            <p class="second">{{item.type}}</p>
            <p class="third">{{''+item.price}}</p>
          </div>
        </li>
      </ul>
    </mt-loadmore>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      allLoaded: false,

      // 
      list: [],
      // 
      page: 1,
      show: false,
      whether: false
    };
  },
  methods: {
    toSort() {
      this.$router.push("/sort");
    },
    touchstart() {
      this.whether = true;
    },
    touchend() {
      this.whether = false;
    },
    // ,
    loadTop() {
      //   console.log(123);
      // 
      this.$axios
        .post("/goods/list", {
          pageNum: 1
        })
        .then(res => {
          const { code, msg, list } = res.data;

          // 
          this.list = list;
          // 1
          this.page = 1;

          // loading
          this.$refs.loadmore.onTopLoaded();
          //   ,,,
          this.allLoaded = false;
        });
    },
    // ,
    loadBottom() {
      // +1
      const newPage = this.page + 1; //

      // 
      this.$axios
        .post("/goods/list", {
          pageNum: newPage
        })
        .then(res => {
          const { code, msg, list } = res.data;

          // 
          if (list.length) {
            // 
            this.list = this.list.concat(list); //

            // 
            this.page = newPage;
          } else {
            // 
            this.allLoaded = true;
          }

          // 
          this.$refs.loadmore.onBottomLoaded();
        });
    },
    toDetail(id) {
      //  id
      // console.log(id)
      this.$router.push("/goodsdetail/" + id);
    }
  },
  //,,
  mounted() {
    this.loadTop();
  }
};
</script>

<style lang="scss" scoped>
.list-container {
  height: 100vh;
  overflow: scroll;

  h2 {
    position: relative;
    text-align: center;
    font-size: 0.23rem;
    line-height: 0.8rem;
    color: #010101;
    letter-spacing: 1.5px;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 999;
    .ic {
      position: absolute;
      top: 0;
      left: 0.3rem;
    }
  }
  .result {
    padding: 0 0.2rem;
    line-height: 0.86rem;
    border-top: 1px solid #b5b5b5;
    font-size: 0.24rem;
    letter-spacing: 2px;
    position: relative;

    .ic-two {
      color: #000;
      position: absolute;
      right: 0.1rem;
      top: 0;
      font-size: 0.3rem;
    }
  }
}

.select-B {
  padding: 0.3rem 0.3rem 0;

  p {
    overflow: hidden;
  }
  p:nth-child(1) {
    padding-left: 0.4rem;
    font-weight: bolder;
    font-size: 0.4rem;
  }
  p:not(:first-child) {
    // 
    font-size: 0.24rem;
    color: #b2b2b2;
    border-bottom: 0.333333px solid #c8c8c8;
  }

  .lastOne {
    border: 0 !important;
  }
  span:nth-child(1) {
    float: left;
  }
  span:nth-child(2) {
    float: right;
    color: #5a7883;
  }
  .btn {
    margin-top: 0.2rem;
    text-align: center;
    background-color: #000;
    color: aliceblue;
    font-size: 0.24rem;
  }

  .touchColor {
    color: #fff;
    background-color: #7f7f7f;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .list-content {
    width: 49%;
    font-size: 0.4rem;

    // &:nth-child(odd) .fisrt {
    //   color: red; // li
    // }

    img {
      width: 100%;
      height: 3.8rem;
      object-fit: cover;
    }

    .pic-introduction {
      padding: 0 10px;
      z-index: 123;

      .frist {
        padding-top: 0.11rem;
        padding-bottom: 0.26rem;
        font-size: 0.24rem;
        color: #000;
      }
      .second {
        font-size: 0.23rem;
        color: #5b5b5b;
        line-height: 0.46rem;
      }
      .third {
        font-size: 0.23rem;
        color: #000;
        letter-spacing: 0.8px;
        padding-bottom: 0.3rem;
        font-family: cursive;
      }
    }
  }
}
</style>