import Vue from 'vue'
import Router from 'vue-router'

//
Vue.use(Router)

import Home from './views/Home.vue'
import Register from './views/Register.vue'
import Login from './views/Login.vue'

import Sort from './views/sort.vue'
import GoodsList from './views/GoodsList.vue'

import GoodsList2 from './views/GoodsList2.vue'
import GoodsDetail from './views/GoodsDetail.vue'

import UserPersonal from './views/UserPersonal.vue'
import  quitLogin  from './views/quitLogin.vue'

import ShopCar from './views/ShopCar.vue'

import GoodsOrder from './views/GoodsOrder'

//  
const router = new Router({
  // mode: 'history',
  mode: 'hash',

  // 
  base: process.env.BASE_URL,

  // 
  routes: [
    
    // 
    {
      path: '/',
      redirect: '/home'
    },

    // 
     {
      path: '/home',
      name: 'home',
      component: Home,
      meta:{
        showNav:true,// 
      }
    },

    // 
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta:{
        showNav:false,// 
      }
    },

    // 
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        showNav:false,
      }
    },

    //
    {
      path: '/sort',
      name: 'sort',
      component: Sort,
      meta:{
        showNav:true,
      }
    },

    // 
    {
      path: '/goodslist',
      name: 'goodslist',
      component: GoodsList,
      meta:{
        showNav:true,
      }
    },
    {
      path: '/goodslist2',
      name: 'goodslist2',
      component: GoodsList2,
      meta:{
        showNav:true,
      }
    },

    // 
    {
      path: '/goodsdetail/:id',
      name: 'goodsdetail',
      component: GoodsDetail,
      meta:{
        showNav:true,
      }
    },
 

    // 
    {
      path: '/userpersonal',
      name: 'userpersonal',
      component:UserPersonal,
      meta:{
        showNav:true,
      }
    },

    // 
   
    {
      path: '/quitlogin',
      name: ' quitlogin',
      component: quitLogin,
      meta:{
        showNav:false,
      }
    },

    //  
    {
      path: '/shopcar',
      name: ' shopcar',
      component: ShopCar,
      meta:{
        showNav:true,
      }
    },

    {
      path: '/goodsorder',
      name: ' goodsorder',
      component: GoodsOrder,
      meta:{
        showNav:false,
      }
    },

    //  

    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
  ]
})

