<template>
  <div>
    <!--  -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <!-- v-for -->
        <template v-for="(item,index) of url">
          <div class="swiper-slide" :key="index" @click="toList">
            <img :src="$commonUrl+item">
          </div>
        </template>
      </div>
      <div class="swiper-pagination"></div>
      <!-- <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div> -->
    </div>
  </div>
</template>

<script>

//
export default {
    name:'swiper',
  data() {
    return {
      url: ""
    }
  },methods: {
    toList(){
      this.$router.push('/goodslist')
    }
  },
  mounted() {
    // axios
  this.$axios
      .post('/goods/swiper'
      )
      .then(res => {
        const url  = res.data.url
        console.log(url)

        // data
        // 
        this.url = url

        // Vue.nextTickthis.$nextTick
        // Vue.nextTick(() => {
        this.$nextTick(() => { 
          // Swiper   
          new Swiper(".swiper-container", {
            // effect:'flip',
            loop: true, // 
            autoplay:{
              delay:1000,
              //disableOnInteraction: true, //,,
            },
            pagination: {
              //
              el: ".swiper-pagination"
            },

            navigation: {
              //
              // nextEl: ".swiper-button-next",
              // prevEl: ".swiper-button-prev"
            }
          })
        })
      })
  }
}
</script>
<style lang="scss" scoped>
    .swiper-container{
        width: 7.5rem;
        height: 3rem;

        .swiper-slide img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }            
    }
</style>
 