<template>
  <div class="set">
    <h2>
      <i class="iconfont ic" @click="toPersonal">&#xe637;</i>
    </h2>
    <ul class="repeat-one">
      <li v-for="(item,index) in list" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <ul class="repeat-two">
      <li v-for="(item,index) in list2" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          {{item.num}}
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <ul class="repeat-three">
      <li v-for="(item,index) in list3" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <ul class="repeat-four">
      <li v-for="(item,index) in list4" :key="index" class="repeat-content">
        {{item.name}}
        <span>
          <i class="iconfont">&#xe64c;</i>
        </span>
      </li>
    </ul>
    <div class="btn" @click="quit"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [{ name: "" }, { name: "" }],
      list2: [
        { name: "" },
        { name: "" },
        { name: "", num: "0.00M" }
      ],
      list3: [{ name: "" }, { name: "" }],
      list4: [{ name: "" }, { name: "APP" }, { name: "" }]
    }
  },methods: {
    toPersonal(){
      this.$router.push('/userpersonal')
    },
    quit(){
     
     // token
       localStorage.removeItem('token')

      //  
       this.$router.push('/home')
         //   
       location.reload()
    }
  },
};
</script>

