  height: 100vh;
  background-color: #f5f5f7;
  color: #000;
  h2 {
    background-color: #fff;
    font-size: 0.34rem;
    padding: 0.3rem 0;
    text-align: center;
    font-weight: 560;
    .ic {
      font-size: 0.5rem;
      position: absolute;
      top: 0.3rem;
      left: 0.3rem;
    }
  }
  .repeat-content {
    background-color: #fff;

    padding: 0 0.6rem;
    line-height: 0.9rem;
    border-bottom: 0.3333px solid #f5f5f7;

    span {
      font-weight: lighter;
